import { LStr } from "../model/LanguageContext";

export const TimestampSpan: React.FC = function () {
  const date = new Date();
  const year = date.getFullYear()
  const month = date.getMonth()
  const day = date.getDate()

  const updatedString = LStr(
    {
      en: `Updated ${year}/${month}/${day}`,
      jp: `${year}年${month}月${day}日　時点`,
    }
  )
  return (
      <span className="timestamp">{updatedString.toString()}</span>
  );
};