import React from "react";
import { LocalizedString } from "../model/LanguageContext";

const Linkify: React.FC<{ children: string; }> = function ({ children }) {
  const spans = children.split(/(\[.+\].+\))/);
  return (
    <>
      {spans.map(function (value, index) {
        const link = value.match(/\[(.+)\]\((.+)\)/);
        return link ? (<a key={index} href={link[2]}>{link[1]}</a>) :
          <span key={index}>{value}</span>;
      })}
    </>
  );
};
export const Text: React.FC<{ children?: LocalizedString; }> = function ({ children }) {
  const localized = children;
  const lines = localized?.toString().split("\n") ?? [];
  return (
    <div>
      {lines.map(function (value, index) {
        return <span key={index}><Linkify>{value}</Linkify><br /></span>;
      })}
    </div>
  );
};
