import React from "react";
import { useContext } from "react";

const LanguageContext = React.createContext({
  lang: "en",
  setLang: (lang: string) => { }
});

class LocalizedString {
  en: String;
  jp?: String;

  constructor(props: { en: String; jp?: String; }) {
    this.en = props.en;
    this.jp = props.jp;
  }

  getText(lang: String) {
  }

  public toString = (): string => {
    const { lang } = useContext(LanguageContext);
    return ((lang === "en") ? this.en : (this.jp ?? this.en)).toString();
  };
}

const LStr = function (props: { en: String; jp?: String; }): LocalizedString {
  return new LocalizedString(props);
};

export {LanguageContext, LocalizedString, LStr}