/** This file should be git ignored so that it's not visible in source control */
import { LocalizedString, LStr } from "../model/LanguageContext";

const EN_ADDRESS = process.env.REACT_APP_ENGLISH_ADDRESS ?? "Tokyo, Japan"
const JP_ADDRESS = process.env.REACT_APP_JAPANESE_ADDRESS ?? "東京"
const EN_MOBILE = process.env.REACT_APP_ENGLISH_MOBILE
const JP_MOBILE = process.env.REACT_APP_JAPANESE_MOBILE
const Email = process.env.REACT_APP_RESUME_EMAIL

const Address: LocalizedString = LStr({
  en: EN_ADDRESS,
  jp: JP_ADDRESS
});

const Mobile: LocalizedString | undefined = EN_MOBILE ? LStr({
  en: EN_MOBILE,
  jp: JP_MOBILE
}) : undefined;


export {Address, Mobile, Email}
