import React, { useContext } from "react";
import {
  Background,
  CareerHistory,
  EducationHistory,
  PersonalInfo,
  Resume,
  Skills,
} from "../model/ResumeTypes";
import { LanguageContext, LocalizedString } from "../model/LanguageContext";
import { Text } from "./TextNode";

export const LocalizedNameView: React.FC<{
  children: LocalizedString;
}> = function ({ children }) {
  const { lang } = useContext(LanguageContext);
  const info = children
  const ruby =
    lang === "jp" ? <div className="ruby-text">{info.jp}</div> : undefined;
  return (
    <div>
      {ruby}
      <h1 className="name">{info.en}</h1>
    </div>
  );
};

const PersonalInfoView: React.FC<{
  info: PersonalInfo;
}> = function (props) {
  const { info } = props;
  const { lang, setLang } = useContext(LanguageContext);
  const toggleEnJp = () => {
    const newLang = lang === "en" ? "jp" : "en";
    setLang(newLang);
  };
  return (
    <>
      <div className="header">
        <LocalizedNameView>{info.name}</LocalizedNameView>
        <div className="contact">
          <Text>{info.address}</Text>
          <br />
          <div>{info.email?.toString()}</div>
          <div>{info.mobile?.toString()}</div>
        </div>
      </div>
      <div id="lang-toggle" onClick={toggleEnJp}>
        <span className="toggle-en">en</span> |{" "}
        <span className="toggle-jp">jp</span>
      </div>
    </>
  );
};

const BackgroundView: React.FC<{
  info: Background;
}> = function (props) {
  const { info } = props;
  return (
    <div className="background-section">
      <h1>{info.sectionTitle.toString()}</h1>
      <Text>{info.prose}</Text>
    </div>
  );
};

const CareerHistoryView: React.FC<{
  info: CareerHistory | EducationHistory;
}> = function (props) {
  const { info } = props;
  return (
    <div>
      <h1>{info.sectionTitle.toString()}</h1>
      {info.prose?.toString()}
      {info.steps.map(function (step, index) {
        const prose = step.prose?.toString();
        return (
          <div key={index}>
            <div className="company-info">
              <h2 className="company-name">{step.company.toString()}</h2>
              <div className="dates">
                {step.joinDate} — {step.endDate}
              </div>
            </div>
            <div className="job-title">
              <h3 className="title">{step.title.toString()}</h3>
            </div>
            {prose ? <Text>{step.prose}</Text> : undefined}
            <ul>
              {step.details.map(function (detail, index) {
                return (
                  <li key={index}>
                    <Text>{detail}</Text>
                  </li>
                );
              })}
            </ul>
          </div>
        );
      })}
    </div>
  );
};

const SkillsView: React.FC<{
  info: Skills;
}> = function (props) {
  const { info } = props;
  return (
    <div>
      <h1>{info.sectionTitle.toString()}</h1>
      {info.prose?.toString()}
      <ul>
        {info.skills.map(function (skill, index) {
          return (
            <li key={index}>
              <Text>{skill}</Text>
            </li>
          );
        })}
      </ul>
    </div>
  );
};

export const ResumeView: React.FC<{
  info: Resume;
}> = function (props) {
  const { info } = props;
  return (
    <div className="hide-unstyled resume">
      <PersonalInfoView info={info.person}></PersonalInfoView>
      <hr />
      <BackgroundView info={info.background}></BackgroundView>
      <hr />
      <CareerHistoryView info={info.career}></CareerHistoryView>
      <hr />
      <div className="page-break"></div>
      <SkillsView info={info.skills}></SkillsView>
      <hr />
      <CareerHistoryView info={info.education}></CareerHistoryView>
    </div>
  );
};
