import React from "react";
import { useContext } from "react";
import { LanguageContext, LStr } from "../model/LanguageContext";
import {
  CareerHistory,
  EducationHistory,
  PersonalInfo,
  Resume,
} from "../model/ResumeTypes";
import { LocalizedNameView } from "./ResumeView";
import { Text } from "./TextNode";
import { TimestampSpan } from "./TimestampView";

const JpExtraInfo: React.FC<{
  info: PersonalInfo;
}> = function (props) {
  const { info } = props;
  const ageStr = info.age?.toString();
  let sexStr = info.sex?.toString() ?? "";
  sexStr = sexStr.length > 0 ? "性別　" + sexStr : "";
  return (
    <>
            {sexStr.length > 0 ? <div>
              {ageStr}
              <br />
              {sexStr}
              <br />
              <br />
            </div> : undefined}
    </>
  )
}

const PersonalInfoView: React.FC<{
  info: PersonalInfo;
}> = function (props) {
  const { lang, setLang } = useContext(LanguageContext);
  const { info } = props;
  const toggleEnJp = () => {
    const newLang = lang === "en" ? "jp" : "en";
    setLang(newLang);
  };

  const docTitle = LStr({
    en: "Work History",
    jp: "履歴書",
  });
  const phone = LStr({
    en: "mobile",
    jp: "電話番号",
  });
  const mail = LStr({
    en: "email",
    jp: "メールアドレス",
  });
  return (
    <>
      <div className="header">
        <div className="column">
          <h1>{docTitle.toString()}</h1>
          <LocalizedNameView>{info.name}</LocalizedNameView>
          <JpExtraInfo info={info}/>
          <div className="contact">
            <Text>{info.address}</Text>
            <br />
            <div className="tabular">
              <div className="column">
                <div><span>{phone.toString()}</span></div>
                <div><span>{mail.toString()}</span></div>
              </div>
              <div className="column">
                <div><span>{info.mobile?.toString()}</span></div>
                <div><span>{info.email?.toString()}</span></div>
              </div>
            </div>
            <div className="tabular"></div>
          </div>
        </div>
        <div className="column right">
          <TimestampSpan />
          <div className="pic-frame"></div>
        </div>
      </div>

      <div id="lang-toggle" onClick={toggleEnJp}>
        <span className="toggle-en">en</span> |{" "}
        <span className="toggle-jp">jp</span>
      </div>
    </>
  );
};

const CareerHistoryView: React.FC<{
  info: CareerHistory | EducationHistory;
}> = function (props) {
  const { info } = props;

  const join = LStr({
    en: "joined",
    jp: "入社",
  });
  const left = LStr({
    en: "left",
    jp: "退社",
  });
  const title = LStr({
    en: "Career History",
    jp: "職歴",
  });

  const steps = info.steps.slice().reverse();
  return (
    <div>
      <h1>{title.toString()}</h1>
      {steps.map(function (step, index) {
        const startDate = step.joinDate;
        const endDate = step.endDate;
        return (
          <div key={index}>
            <div className="history-one-line">
              <div className="left">{startDate}</div>
              <div className="right">{step.company.toString()}</div>
              <div className="join-leave">{join.toString()}</div>
            </div>
            <div className="history-one-line">
              <div className="left">{endDate}</div>
              <div className="right">{step.company.toString()}</div>
              <div className="join-leave">{left.toString()}</div>
            </div>
          </div>
        );
      })}
    </div>
  );
};

const EducationHistoryView: React.FC<{
  info: EducationHistory;
}> = function (props) {
  const { info } = props;

  const graduated = LStr({
    en: "graduated",
    jp: "卒業",
  });

  const steps = info.steps.slice().reverse();
  return (
    <div>
      <h1>{info.sectionTitle.toString()}</h1>
      {steps.map(function (step, index) {
        const endDate = step.endDate;
        return (
          <div key={index}>
            <div className="history-one-line">
              <div className="left">{endDate}</div>
              <div className="right column">
                <div className="school-name">{step.company.toString()}</div>
                <div className="degree-name">{step.title.toString()}</div>
              </div>
              <div className="join-leave">{graduated.toString()}</div>
            </div>
          </div>
        );
      })}
    </div>
  );
};

const LanguageSkill: React.FC = function () {
  const title = LStr({
    en: "Language Ability",
    jp: "語学",
  });

  const enLang = LStr({
    en: "English",
    jp: "英語",
  });

  const enAbility = LStr({
    en: "Native",
    jp: "ネイティブ",
  });

  const jpLang = LStr({
    en: "Japanese",
    jp: "日本語",
  });

  const jpAbility = LStr({
    en: "Fluent - Receiving the highest certification of [N1](http://www.jlpt.jp/e/about/levelsummary.html) on the JLPT test in 2012.",
    jp: "ビジネスレベル\n[日本語能力試験でN1(一級)認証](http://www.jlpt.jp/about/levelsummary.html)",
  });

  return (
    <div>
      <h1>{title.toString()}</h1>
      <div>
        <div className="history-one-line">
          <div className="left">{enLang.toString()}</div>
          <div className="right">{enAbility.toString()}</div>
        </div>
        <div className="history-one-line">
          <div className="left">{jpLang.toString()}</div>
          <div className="right">
            <Text>{jpAbility}</Text>
          </div>
        </div>
      </div>
    </div>
  );
};

export const WorkHistoryView: React.FC<{
  info: Resume;
}> = function (props) {
  const { info } = props;
  return (
    <div>
      <PersonalInfoView info={info.person}></PersonalInfoView>
      <EducationHistoryView info={info.education}></EducationHistoryView>
      <CareerHistoryView info={info.career}></CareerHistoryView>
      <LanguageSkill />
    </div>
  );
};
