import React, { useState } from "react";
import "../App.css";
import { JakeResume } from "../data/Resume";
import { LanguageContext} from "../model/LanguageContext";
import { ResumeView } from "../views/ResumeView";
import { WorkHistoryView } from "../views/WorkHistoryView";

function ResumePage() {
  const [lang, setLang] = useState("en");
  const [doc, setDoc] = useState("resume");
  const toggleDoc = () => {
    const newDoc = doc === "resume" ? "hist" : "resume";
    setDoc(newDoc);
  };

  return (
    <LanguageContext.Provider value={{ lang: lang, setLang: setLang }}>
      <div className={"App lang-set-" + lang}>
        {doc === "resume" ?
          <ResumeView info={JakeResume}></ResumeView> :
          <WorkHistoryView info={JakeResume}></WorkHistoryView>
        }
        <div id="doc-toggle" className={doc} onClick={toggleDoc}>
          <span className="toggle-resume">resume</span>{" | "}
          <span className="toggle-hist">hist</span>
        </div>
      </div>
    </LanguageContext.Provider>
  );
}

export default ResumePage;
